import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Projet } from 'src/app/models/projet';
import { Stardevcgroup } from 'src/app/models/stardevcgroup';
import { StardevcgroupService } from 'src/app/services/stardevcgroup.service';
import { flyInOut, expand } from './../../../animations/app.animation'

@Component({
  selector: 'app-page-contact-one',
  templateUrl: './page-contact-one.component.html',
  styleUrls: ['./page-contact-one.component.css'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class PageContactOneComponent implements OnInit {

  formErrors = {
    'auteur': '',
    'email': '',
    'titre': '',
    'description':''
  };

  validationMessages = {
    'auteur': {
      'required':      'le nom de l\'auteur est requis.',
      'minlength':     'le nom l\'auteur être plus que 4 caractères.',
      'maxlength':     'le nom l\'auteur ne doit pas être plus que 150 caractères.'
    }, 
    'email': {
      'required':      'l\'email nom est requis.',
      'minlength':     'l\'email être plus que 4 caractères.',
      'maxlength':     'l\'email ne doit pas être plus que 150 caractères.'
    },
    'titre': {
      'required':      'le titre est requis.',
      'minlength':     'le titre être plus que 5 caractères.',
      'maxlength':     'le titre ne doit pas être plus que 150 caractères.'
    }, 
    'description': {
      'required':      'la description titre est requise.',
      'minlength':     'la description être plus que 10 caractères.',
      'maxlength':     'la description ne doit pas être plus que 2000 caractères.'
    }, 
  };

  mailPattern: string = "[@]";
  projet: any;
  reponse: string;
  msgErr: string;

  @ViewChild('fform', {static: false}) projetFormDirective;
  projetForm: FormGroup;
  stardevcgroup: Stardevcgroup;
  constructor(
    public stardevcgroupService: StardevcgroupService,
    private formBuilder: FormBuilder,
    ) { 
    }

  ngOnInit(): void {
   
    this.stardevcgroupService
        .getStardevcgroups()
        .subscribe(data=>{
          this.stardevcgroup = data[0];
        }) ;
        this.createForm();   
  }

  createForm() {
    this.projetForm = this.formBuilder.group({
      auteur: ['',  [Validators.required, Validators.minLength(4), Validators.maxLength(150)] ],
      email: ['', [ Validators.required, Validators.minLength(5), Validators.email ] ],
      titre: ['', [ Validators.required,  Validators.minLength(5), Validators.maxLength(150)] ],
      description: ['',  [Validators.required, Validators.minLength(10), Validators.maxLength(2000)] ]
    });
    this.projetForm
        .valueChanges
        .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.projetForm) { return; }
    const form = this.projetForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }


  
  onSubmit() {
    this.projet = this.projetForm.value;
    this.createProjet( this.projet );
    console.log(this.projet);
    this.projetForm.reset({
      titre: '',
      nom: 0,
      description: '',
      email: ''
    });
    this.projetFormDirective.resetForm();
  }

  createProjet( projet: Projet ) {
      this.stardevcgroupService
          .createProjet( projet )
          .subscribe( data => {
            this.reponse = 'Le projet: ' + data.titre + ' a été soumis à l\'administration de Star Dev Cloud avec succèss, nous rviendrons dans un bref delai !!!'; 
          } )
  }



}
