import { Component, OnInit } from '@angular/core';
import { NgbCarouselConfig } from '@ng-bootstrap/ng-bootstrap';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { SwiperOptions } from 'swiper';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Stardevcgroup } from 'src/app/models/stardevcgroup';
import { StardevcgroupService } from 'src/app/services/stardevcgroup.service';
import { flyInOut, expand } from './../../../animations/app.animation'

@Component({
  selector: 'app-index-corporate',
  templateUrl: './index-corporate.component.html',
  styleUrls: ['./index-corporate.component.css'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class IndexCorporateComponent implements OnInit {

  stardevcgroup: Stardevcgroup;
  indexAleatoire:number=0;
  icons: string[] = ["uil uil-wordpress", "uil uil-create-dashboard", "uil uil-setting", "uil uil-mobile-vibrate"]
  sommesIcon:string[] = ["monitor", "heart", "eye", "settings"];

  constructor(private modalService: NgbModal, public stardevcgroupService: StardevcgroupService) {}
  navClass = 'nav-light';
  public config: SwiperOptions = {
    a11y: { enabled: true },
    direction: 'horizontal',
    slidesPerView: 1,
    keyboard: true,
    navigation: true,
    pagination: false
  };

  customOptions: OwlOptions = {
    loop: true,
    mouseDrag: true,
    touchDrag: false,
    pullDrag: false,
    navSpeed: 700,
    navText: ['', ''],
    responsive: {
      0: {
        items: 1
      },
      600: {
        items: 2
      },
      900: {
        items: 3
      }
    },
    nav: false
  };
  /**
    * Open modal for show the video
    * @param content content of modal
    */
  openWindowCustomClass(content) {
    this.modalService.open(content, { windowClass: 'dark-modal', size: 'lg', centered: true });
  }
  onstructor(config: NgbCarouselConfig) {
    config.interval = 2000;
    config.wrap = true;
    config.keyboard = false;
    config.pauseOnHover = false;
  }
  ngOnInit(): void {
    this.stardevcgroupService
        .getStardevcgroups()
        .subscribe(data=>{
          this.stardevcgroup = data[0];
          this.indexAleatoire = Math.round( this.stardevcgroup.slogans.length * Math.random() );
        })     
  }
}
