import { Component, OnInit } from '@angular/core';
import { Stardevcgroup } from 'src/app/models/stardevcgroup';
import { StardevcgroupService } from 'src/app/services/stardevcgroup.service';
import { flyInOut, expand } from './../../../animations/app.animation'

@Component({
  selector: 'app-page-job-company',
  templateUrl: './page-job-company.component.html',
  styleUrls: ['./page-job-company.component.css'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class PageJobCompanyComponent implements OnInit {
  stardevcgroup: Stardevcgroup;
  constructor(public stardevcgroupService: StardevcgroupService) { }
  ngOnInit(): void {
    this.stardevcgroupService
        .getStardevcgroups()
        .subscribe(data=>{
          this.stardevcgroup = data[0];
        }) ;
  }

}
