import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexCorporateComponent } from './core/index-corporate/index-corporate.component';
import { MasterPageComponent } from './core/master-page/master-page.component'
import { PageAboutusTwoComponent } from './core/page-aboutus-two/page-aboutus-two.component';
import { PageContactOneComponent } from './core/page-contact-one/page-contact-one.component';
import { PageJobCompanyComponent } from './core/page-job-company/page-job-company.component';
import { PageServicesComponent } from './core/page-services/page-services.component';

import { SwitcherComponent } from './shared/switcher/switcher.component';

const routes: Routes = [
  {
    path: '',
    component: MasterPageComponent,
    children: [
      { path: '', component: IndexCorporateComponent },
      { path: 'accueil', component: IndexCorporateComponent },
      { path: 'apropos', component: PageAboutusTwoComponent  },
      { path: 'services', component: PageServicesComponent },
      { path: 'contact', component: PageContactOneComponent },
      { path: 'apropos-nous', component: PageJobCompanyComponent },
      { path: '#', component: SwitcherComponent },
    ]
  }

];

@NgModule({
  imports: [RouterModule.forRoot(routes, { scrollPositionRestoration: 'top', relativeLinkResolution: 'legacy' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
