import { Component, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Stardevcgroup } from 'src/app/models/stardevcgroup';
import { StardevcgroupService } from 'src/app/services/stardevcgroup.service';
import { flyInOut, expand } from './../../../animations/app.animation'

@Component({
  selector: 'app-page-aboutus-two',
  templateUrl: './page-aboutus-two.component.html',
  styleUrls: ['./page-aboutus-two.component.css'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class PageAboutusTwoComponent implements OnInit {
  navClass = 'nav-light';
  stardevcgroup:Stardevcgroup=null; 
  constructor(
    private modalService: NgbModal, 
    public stardevcgroupService: StardevcgroupService,
    ) { }

  ngOnInit(): void {
    this.stardevcgroupService
        .getStardevcgroups()
        .subscribe(data=>{
          this.stardevcgroup = data[0];
        })     
  }

  /**
    * Open modal for show the video
    * @param content content of modal
    */
  openWindowCustomClass(content) {
    this.modalService.open(content, { windowClass: 'dark-modal', size: 'lg', centered: true });
  }

}
