import { Component, OnInit } from '@angular/core';
import { Stardevcgroup } from 'src/app/models/stardevcgroup';
import { StardevcgroupService } from './../../services/stardevcgroup.service';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {
  stardevcgroup: Stardevcgroup;
  eachYear: number = 0;
  constructor(public stardevcgroupService: StardevcgroupService) { }

  ngOnInit(): void {
    this.eachYear = new Date().getFullYear();
    this.stardevcgroupService
        .getStardevcgroups()
        .subscribe(data=>{
          this.stardevcgroup = data[0];
        })     
  }

}
